.recipe {
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 10px;
    box-shadow: 0 5px 20px rgba(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: #fff;
    align-items: center;
    min-width: 40%;
}

.image {
    border-radius: 50%;
}
